<template>
	<div class="s-index-wrapper">
		<YHeader :logo="require('@/assets/img/logo_white.png')" bgColor="transparent" :isPlaceholder="false">
			<YNavbar :list="navbarList" />
		</YHeader>
		<hooper ref="planetManHooper" class="s-index-banner" style="height:100%;" vertical>
			<slide index="1">
				<div class="content content1 yu-flex-center">
					<FlipClock />
				</div>
			</slide>
			<slide index="2">
				<div class="content content2">
					<div class="particles" :style="{height:innerHeight + 'px'}" id="particlesId"></div>
				</div>
			</slide>
      <template v-slot:hooper-addons>
        <hooper-pagination ></hooper-pagination>
      </template>
		</hooper>
	</div>
</template>

<script>
	import FlipClock from '@/components/FlipClock.vue'
	import YHeader from '@/components/YHeader.vue'
	import YNavbar from '@/components/YNavbar.vue'
	import { Hooper, Slide, Pagination as HooperPagination } from 'hooper';
	import * as particlesJs from 'particles.js'
	import particlesJson from "@/utils/particles.json"
	import 'hooper/dist/hooper.css';
	export default {
		components: {
			YHeader,
			YNavbar,
			Hooper,
			HooperPagination,
			Slide,
			FlipClock
		},
		data() {
			return {
				innerHeight:1440
			}
		},
		mounted() {
			this.innerHeight = window.innerHeight;
			this.$nextTick(()=>{
				particlesJS("particlesId", particlesJson)
			})
			window.addEventListener('resize', ()=>{
				this.innerHeight = window.innerHeight;
			})
		}
	}
</script>

<style lang="scss" scoped>
	.s-index-wrapper {
		height: 100%;

		.s-index-banner {
			.content {
				background-repeat: no-repeat;
				background-position: center;
				background-size: cover;
				height: 100%;
			}

			.content1 {
				// background-image: linear-gradient(to right, rgba(44, 0, 126, 0.7490196078), #060533);
				background-color: #101018;
			}

			.content2 {
				// background-image: linear-gradient(90deg, #3e43e2 0%, #7a17f9 100%);
				width: 100%;
				height: 100%;
				overflow: hidden;
				.particles{
					position: relative;
					width: 100%;
					background:linear-gradient(0deg,rgb(7, 33, 99),rgb(3, 17, 51));
					background-repeat: no-repeat;
					background-size: cover;
					background-position: 50% 50%;
					font-size: 0;
					line-height: 0;
				}
			}

			.content3 {
				background-image: linear-gradient(90deg, #3e43e2 0%, #3c8bf7 100%);
			}
		}
	}
</style>
